<template>
    <div>
        <mu-appbar style="width: 100%;" color="primary">
            <mu-button icon slot="left" @click="$router.replace({name:'video'})">
                <mu-icon value="arrow_back"></mu-icon>
            </mu-button>
            
        </mu-appbar>
        <mu-container>
            <mu-form ref="form" :model="validateForm" class="mu-demo-form mu-padding">
                <mu-form-item label="" help-text="" prop="username" :rules="usernameRules">
                    <mu-text-field v-model="validateForm.username" prop="username"></mu-text-field>
                </mu-form-item>
                <mu-form-item label="" prop="password" :rules="passwordRules">
                    <mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
                </mu-form-item>
                <mu-form-item label="" prop="password" :rules="confirmPasswordRules">
                    <mu-text-field type="password" v-model="validateForm.confirmPassword" prop="password"></mu-text-field>
                </mu-form-item>
                <mu-form-item>
                    <mu-button color="primary" @click="submit"></mu-button>
                    <mu-button @click="clear"></mu-button>
                </mu-form-item>
            </mu-form>
        </mu-container>
    </div>
</template>
<script>
export default {
    data() {
        return {
            //
            usernameRules: [
                { validate: val => !!val, message: "" },
                { validate: val => val.length >= 3, message: "3" }
            ],
            passwordRules: [
                { validate: val => !!val, message: "" },
                {
                    validate: val => val.length >= 3 && val.length <= 10,
                    message: "310"
                },
                {
                    validate: val => val == this.validateForm.confirmPassword,
                    message: ""
                }
            ],
            confirmPasswordRules: [
                { validate: val => !!val, message: "" },
                {
                    validate: val => val.length >= 3 && val.length <= 10,
                    message: "310"
                },
                {
                    validate: val => val == this.validateForm.password,
                    message: ""
                }
            ],
            //
            validateForm: {
                username: "",
                password: "",
                confirmPassword: ""
            }
        };
    },
    methods: {
        //
        submit() {
            this.$refs.form.validate().then(result => {
                if (result) {
                    console.log(this.validateForm);
                    const pwd = this.crypto
                        .createHash("md5")
                        .update(this.validateForm.password)
                        .digest("hex");
                    const name = this.validateForm.username;
                    const url = this.global_.getApiConfig().register;
                    const router = this.$router;
                    this.$http
                        .post(url, {
                            username: name,
                            password: pwd
                        })
                        .then(function(res) {
                            console.log(res.data);
                            if (res.data.data) {
                                alert("");
                                router.replace({ name: "video" });
                            } else {
                                alert("");
                            }
                        })
                        .catch(function(err) {
                            alert(err);
                        });
                }
            });
        },
        // 
        clear() {
            this.$refs.form.clear();
            this.validateForm = {
                username: "",
                password: "",
                confirmPassword: ""
            };
        }
    }
};
</script> 
<style scoped>
.mu-demo-form {
    width: 100%;
    max-width: 460px;
}
.mu-padding {
    padding-top: 120px;
    padding-right: 40px;
    padding-left: 40px;
}
</style>


